.parts{
  background-color: #fff;
  font-size: 16px;
  margin-bottom: 20px;
}
.parts .module-info{
  display: flex;
  height: 48px;
  border-bottom: 1px solid #f2f2f2;
  justify-content: space-between;
  align-items: center;
  padding:0 22px 0 10px;
}
.parts .module-info .module-name{
  height: 15px;
  line-height: 15px;
  border-left: 4px solid #fdd000;
  text-indent: 5px;
  color:#1a1a1a;
}

.table-wrap{
  padding: 16px 20px;
  background-color: #fff;
}
.parts-table{
  width: 100%;
  border: 1px solid #dbdbdb;
}
.parts-table .t-item{
  display: flex;
  height: 48px;
  line-height: 48px;
}
.t-item .order{
  width: 50px;
  text-align: center;
}
.t-item .name{
  width: 220px
}
.t-item .oe{
  width: 242px;
  padding-right: 30px;
}
.t-item .num{
  width: 60px;
}
.t-item .num .ant-input{
  text-align: center;
}
.t-item .unit{
  width: 80px;
  padding-right: 30px;
}
.t-item .unit .ant-input{
  text-align: center;
}
.t-item .remark{
  flex:1;
  padding-right: 30px;
}
.t-item .action{
  width: 68px;
  text-align: center;
  cursor: pointer;
}
.t-item .is-required{
  color: red
}
.parts-table thead .t-item{
  background-color: #fafafa;
}
thead .order,thead .name,thead .oe,thead .num,thead .unit,thead .remark{
  text-align: center;
}
.parts-table tbody .t-item{
  border-top: 1px solid #dbdbdb;
}
.icon-shanchu{
  font-size: 20px;
}