.inside-footer .service-info{
  background-color: #f0f0f0;
}
.inside-footer .service-info .service-wrap{
  box-sizing: border-box;
  display: flex;
  align-items: center;
  height: 80px;
  padding:20px 0 22px;
}
.service-wrap .footer-service-item{
  flex:1;
  display: flex;
  align-items: center;
  padding-left:64px;
}
.footer-service-item .icon-size{
  font-size: 40px;
  margin-right: 10px;
  color: #666;
}
.service-desc{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.service-desc strong{
  font-size: 16px;
  line-height: 16px;
  display: block;
  margin-bottom: 6px;
}
.service-desc span{
  font-size: 16px;
  line-height: 16px;
  display: block;
}

// 底部第二部分样式
.inside-footer .consultation{
  display: flex;
  height: 160px;
  padding:30px 0;
  margin:0 auto;
  border-bottom:1px solid #e0e0e0;
}
.consultation .consult-item{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color:#666666;
}
.consult-item .nav-link{
  width:178px;
  height: 80px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.consult-item .nav-link button{
  color:#666666;
  cursor: pointer;
  background-color: #fafafa;
}
.consult-item .nav-link a:nth-child(odd){
  margin-right: 40px;
}
.consultation .consult-item:nth-child(2){
  border-left:1px solid #e0e0e0;
  border-right:1px solid #e0e0e0;
}
.consult-item .qr-code{
  width: 100px;
  height:100px;
  margin-left: 20px;
}
.consultation .service-phone{
  margin-left: 82px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.consultation .service-phone p:nth-child(even){
  color:#333;
}

// 公司信息
.inside-footer .company-info{
  display: flex;
  height: 66px;
  line-height: 52px;
  justify-content: space-between;
  padding:14px 108px 0;
  font-size: 14px;
}
.company-info button{
  background-color: #fafafa;
}
.inside-footer .record{
  text-align: center;
}