// 左侧状态
 .offer-State{
  height: 84px;
  padding: 20px;
  color: #2f2f2f;
  border: 1px solid #fafafa;
}
.offer-State .state-btn{
  display: block;
  width: 54px;
  height: 24px;
  color: #2f2f2f;
  background-color: #fdd000;
}
.offer-State .offer-number{
  line-height: 30px;
  color: #2f2f2f;
}

// 左侧车辆
.offer-car{
  display: flex;
  // min-height: 102px;
  padding: 20px 10px;
  border-bottom: 1px solid #fafafa;
}
.offer-car .car-image{
  width: 27px;
  height: 27px;
  margin-right: 5px;
}
.offer-car .car-desc{
  flex:1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.offer-car .car-desc p{
  color: #666;
}
.offer-car .car-desc .car-number{
  font-size: 14px;
  color: #333;
}

// 左侧要求
.offer-request{
  padding: 20px 10px 10px;
  border-bottom: 20px solid #fafafa; 
}
.offer-request .request-item{
  display: flex;
}
.request-item .request-name{
  color: #333;
  width: 64px;
  margin-bottom: 10px;
}
.request-desc{
  flex: 1;
}
.offer-parts{
  background-color: #fff;
  padding: 20px 10px;
}
.name-list{
margin-top: 10px;
}
.name-list .name-item{
  display: block;
  font-size: 12px;
  color:#333333;
  line-height: 34px;
  margin-bottom: 6px;
}
.name-list .name-item .parts-name{
  display: inline-block;
  margin-right: 4px;
}
.name-list .name-item .parts-num{
  display: inline-block;
  color:#666666;
}