.count-com {
    display: inline-flex;
    width: 80px;
    height: 20px;
    line-height: 20px;
    background-color: #ffffff;
    border-radius: 2px;
    border: solid 1px #dbdbdb;
    .reduce,.add {
        text-align: center;
        flex: 1;
        font-size: 20px;
        line-height: 13px;
        cursor: default;
    }
    .num{
        width: 28px;
        padding: 0 5px;
        text-align: center;
        border-left: 1px solid #dbdbdb;
        border-right: 1px solid #dbdbdb;
    }
    
    
}