$theme-color: #fdd000; //主题色
$warn-color:#f33; //警告颜色
$border-color:#ccc; //边框颜色
$light-color:#999; //次级文字颜色
$lighter-color:#666; //次级文字颜色
$normal-color:#1a1a1a; //次级文字颜色

.tip{
    color: #d47e4f;
    cursor: pointer;
}
.mb15{
    margin-bottom: 15px;
}

.register {
    margin: 0 auto;
    font-size: 14px; // margin: 0 auto;
    .register-header {
        margin:20px 0 13px;
        // .clearfix
        display: flex;
        justify-content: space-between;
        align-items: center;
        .left{
            // float: left;
            color: #4d4d4d;
            font-size: 24px;
        }
        .square{
            display: inline-block;
            margin-right: 5px;
            width: 4px;
            height: 20px;
            background-color: $theme-color;
        }
        
        .right{
            // float: right;
        }

    }
    .register-container {
        height: 830px;
        padding: 40px 0 150px;
        background-color: #fff;
        margin: 0 auto;
        position: relative;
        .register-content-top {
            width: 900px;
            margin: 0 auto;
            border-bottom: 1px solid #f0f0f0;
            display: flex;
            margin-bottom: 60px;
            padding-bottom: 16px;
            &>span {
                height: 25px;
                text-align: center;
                flex: 1px;
                position: relative;
                color: $lighter-color;
                .cir {
                    display: inline-block;
                    height: 10px;
                    width: 10px;
                    border-radius: 50%;
                    background-color: $light-color;
                    position: absolute;
                    // left: 144px;
                    left: 220px;
                    // top: 20px;
                    top: 36px;
                }
            }
            &>span.active {
                color: $normal-color;
                .cir {
                    background-color: $theme-color;
                }
            }
        }
        .register-footer {
            position: absolute;
            left: 150px;
            bottom: 40px;
            width: 900px;
            margin: 0 auto;
            font-size: 12px;
            color: $light-color;
        }
        .ant-input {
            width: 280px;
            height: 46px;
            margin-bottom: 15px;
            border-radius: 4px;
        }
        .ant-btn {
            height: 46px;
        }
        .label-input {
            width: 130px;
            display: inline-block;
            color: $lighter-color;
            margin-right: 15px;
            text-align: right;
            i {
                color: $warn-color;
            }
        }
        .vali {
            // width: 600px;
            margin-left: 300px;
            .code.ant-input {
                width: 166px;
                margin-right: 12px;
            }
            .getcode {
                margin-right: 15px;
            }
            .valiphone-item {
                margin: 0 0 15px 145px;
            }
            .complete {
                width: 200px;
                background-color: $theme-color;
                border-color: $theme-color;
                font-size: 18px;
                color: #332200;
            }
        }
    }
    .valiAccount-cascader{
   
        .ant-cascader-picker-label,.ant-cascader-picker-arrow,.ant-cascader-picker-clear{
            top: 24px;
        }
    }
    .ant-cascader-menu{
        min-width: 190px;
    }
}