.orderDetail.container {
    padding: 20px 20px 40px;
    margin: 20px auto;
    font-size: 14px;
    color: #666;
    h4 {
        font-size: 16px;
        color: #333333;
        margin-bottom: 18px;
    }
    .detail-header {
        margin-bottom: 14px;
        padding: 20px;
        position: relative;
        color: #333;
        background-color: #fff;
        p {
            color: #999999;
        }
        strong {
            font-size: 20px;
            font-weight: normal;
            color: #ff6633;
        }
        .complete {
            color: #48c651;
        }
        .wait {
            color: #ff6633;
        }
        .ant-btn {
            width: 77px;
            height: 28px;
            position: absolute;
            right: 20px;
            top: 25px;
            background-color: #ffffff;
            border-radius: 4px;
            border: solid 1px #1882f8;
            color: #1882f8;
            padding: 0;
        }
        .toPay{
            color: #d47e4f;
            border: solid 1px #d47e4f;
        }
    }
    .detail-con {
        background-color: #fff;
        .orderInfo,
        .expressInfo,
        .goodsInfo {
            padding: 20px;
        }
        .orderInfo {
            border-bottom: 1px solid #e1e1e1;
            p {
                color: #999999;
            }
        }
        .expressInfo {
            border-bottom: 1px solid #e1e1e1;
            p.expressP {
                color: #999999;
                margin-bottom: 15px;
            }
            .expressInfo-con {
                display: flex;
                .expressCon-part {
                    width: 500px;
                }

                    .hmsTime{
                        margin: 0 14px;
                    }
            //    .sub{

            //        .hmsTime{
            //         //    margin-left: 50px;
            //        }
            //    }
            }
        }
        .goodsInfo {}
    }
    .part,
    .partInfo {
        // width: 500px;
        padding-left: 12px;
    }
    .price,
    .count,
    .unit,
    .amount {
        width: 165px;
        text-align: center;
    }
    .amount {
        width: 160px;
    } //公司
    .orderItem {
        border: solid 1px #e0e0e0;
        border-bottom: none;
        margin-bottom: 10px;
        .item-header {
            height: 34px;
            line-height: 34px;
            background-color: #ffffff;
            border-bottom: solid 1px #e0e0e0;
            padding-left: 16px;
        }
        .item-con {
            display: flex;
            .item-partInfo {
                display: flex;
                flex-direction: column;
                flex: 1;
                border-right: 1px solid #e0e0e0;
                border-bottom: solid 1px #e0e0e0;

                color: #999;
                &>div {
                    display: flex;
                    flex: 1;
                    align-items: center;
                    padding-left: 16px;
                    height: 34px;
                    line-height: 34px;
                    
                    .part {
                        flex: 1;
                        color: #666;
                    }
                }
            }
            .toPay {
                color: #d47e4f;
            }
            .confirmBtn {
                color: #1882f8;
            }
            button.confirmBtn {
                border: solid 1px #1882f8;
            }
           
            button.toPay {
                border: solid 1px #d47e4f;
            }
            .realAmount,
            .state,
            .operation {
                padding: 10px;
                flex-direction: column;
                display: flex;
                align-items: center;
                justify-content: center;
                border-right: 1px solid #e0e0e0;
                border-bottom: 1px solid #e0e0e0;
                color: #999;
            }
            .realAmount {
                font-size: 18px;
                color: #333;
            }
            .operation {
                border-right: none;
            }
            button {
                width: 77px;
                height: 28px;
                border-radius: 4px;
                border: solid 1px #1882f8;
            }
        }
    }
    .orderItem:hover{
        .item-partInfo{
            // &>div:last-of-type{
                border-bottom: none;
            // }
        }
        .realAmount,
            .state,
            .operation{
                border-bottom: none;
            } 

    }
    .realAmount {
        text-align: center;
        width: 150px;
    }
    .state {
        text-align: center;
        width: 120px;
    }
    .operation {
        text-align: center;
        width: 100px;
    }
}