.container.offer-wrap{
  background-color: #fff;
  margin: 20px auto;
}
.ant-tabs-bar{
  margin-bottom:0;
}
.offer-count{
  display: inline-block;
  color: #f33;
  margin-left:6px; 
}
.offer-search{
  display: flex;
  padding:20px 20px 17px;
}
.offer-search .offer-condition{
  display: flex;
  align-items: center;
}
.offer-condition .name{
  color:#333;
  margin-right: 18px;
}
.offer-condition .ant-input{
  height:36px;
}
.offer-query{
  position: relative;
  margin-right: 23px;
}
.offer-btn{
  width: 62px;
  height: 36px;
  line-height: 36px;
  background-color: #fdd000;
  color:#333;
  border-radius: 4px;
}
.offer-table{
  padding: 0 20px;
}
//banner 样式切换覆盖
.offer-table .ant-table-content{
  min-height: 480px;
}
.ant-tabs-nav .ant-tabs-tab-active,.ant-tabs-nav .ant-tabs-tab:hover{
  color:#333;
}
.ant-tabs-ink-bar{
  background-color: #fdd000;
}
.ant-select-enabled{
  height: 36px;
  width: 177px;
}
.name-wrap{
  position: absolute;
  width: 100%;
  border-radius: 5px;
  top: 38px;
  background-color: #fff;
  box-shadow: 3px 3px 10px #888888;
  z-index: 10;
}
.inquery-name{
  height: 36px;
  line-height:36px;
  padding:0 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  border-bottom:1px solid #f5f5f5; 
}
.inquery-name:hover{
  background-color: #fdd000;
  color:#333;
}
.link-other{
  display: block;
  width: 100%;
  height: 100%;
  text-indent: 4px;
}