.orderManage.container {
    font-size: 14px;
    color: #4d4d4d;
    margin: 15px auto 28px;
    padding: 0 15px 30px 30px;
    background-color: #fff;
    .order-tab {
        .option {
            margin-right: 40px;
            text-align: center;
            display: inline-block;
            width: 100px;
            font-size: 16px;
            color: #666;
            height: 47px;
            line-height: 47px;
        }
        .active {
            border-bottom: 3px solid #fdd000;
            color: #1a1a1a;
        }
    }
    .search {
        padding: 20px 0;
        label {}
        input {
            margin: 0 30px 0 10px;
            width: 180px;
            height: 36px;
            background-color: #ffffff;
            border-radius: 4px;
            border: solid 1px #dbdbdb;
            padding: 4px 11px;  
        }
        button{
                width: 62px;
                height: 36px;
                line-height: 36px;
                color:#333;
                background-color: #fdd000;
                border-radius: 4px;
        }
        .ant-calendar-picker-clear,
        .ant-calendar-picker-icon {
            right: 42px;
        }
        .ant-calendar-picker:last-of-type {
            margin-left: 20px;
        }
    }
    .order-con {
        .con-header {
            display: flex;
            height: 44px;
            padding-left: 16px;
            line-height: 44px;
            margin-bottom: 12px;
            background-color: #fafafa;
            color: #999;
            border: solid 1px #e0e0e0;
        }
        .goodInfo {
            flex: 1; // width: 500px;
        }
        .price {
            text-align: center;
            width: 90px;
        }
        .count {
            text-align: center;
            width: 90px;
        }
        .realAmount {
            text-align: center;
            width: 150px;
        }
        .state {
            text-align: center;
            width: 120px;
        }
        .operation {
            text-align: center;
            width: 100px;
        }
    }
    .orderItem {
        border: solid 1px #e0e0e0;
        border-bottom: none;
        margin-bottom: 10px;
        .item-header {
            height: 34px;
            line-height: 34px;
            background-color: #ffffff;
            border-bottom: solid 1px #e0e0e0;
            padding-left: 16px;
        }
        .item-con {
            display: flex;
            .item-partInfo {
                display: flex;
                flex-direction: column;
                flex: 1;
                border-right: 1px solid #e0e0e0;
                color: #999;
                border-bottom: solid 1px #e0e0e0;
                &>div {
                    display: flex;
                    align-items: center;
                    flex:1;
                    padding-left: 16px;
                    height: 34px;
                    line-height: 34px;
                   
                    .part{
                        flex:1;
                        color: #666;
                    }
                }
            }
           
            .realAmount,
            .state,
            .operation {
                flex-direction: column;
                display: flex;
                align-items: center;
                padding: 10px;
                justify-content: center;
                border-right: 1px solid #e0e0e0;
                border-bottom: 1px solid #e0e0e0;
                color: #999;
            }
            .realAmount{
                font-size: 18px;
                color: #333;
            }
            .operation {
                border-right: none;
            }
             button{
                width: 77px;
                height: 28px;
                border-radius: 4px;
               
            }
        }
    }
    .orderItem:hover{
        .item-partInfo{
            // &>div:last-of-type{
                border-bottom: none;
            // }
        }
        .realAmount,
            .state,
            .operation{
                border-bottom: none;
            } 

    }
    .mr20 {
        margin-right: 20px;
    }
    .mr15 {
        margin-right: 15px;
    }
    .confirmBtn{
        color: #1882f8;
        // border-color: #1882f8;
    }
    button.confirmBtn{
    border: solid 1px #1882f8;
    }
    .toPay{
        color: #d47e4f;
    }
    button.toPay{
        border: solid 1px #d47e4f;
    }
}