.shopCar.container{
    box-sizing: border-box;
    
    padding:0px 20px 20px;
    margin: 20px auto 40px;
    font-size: 14px;
    color:#666;
  background-color: #fff;

    .header{
        height: 44px;
        line-height: 44px;
        display: flex;
        align-items: center;
     
        
    }
    .content,.footer{
        // border: 1px solid #e0e0e0;
    }
    .content{
        border-bottom: none ;
    }
    .footer-fixed{

            width: 100%;
            position: fixed;
            left: 0;
            bottom: 0;
            background-color: #ffffff;
            box-shadow: 0px -10px 17px 0px 
                rgba(231, 231, 231, 0.22);
                .footer{
                    width: 1200px;
                    margin: 0 auto;
                    padding:0 20px;
                    border: none;
                }
    }
    .footer{
        height: 50px;
        line-height: 50px;
        border: 1px solid #e0e0e0;
        border-top: none ;
        // display: flex;
        .selected,.invalid{
            margin: 10px;
        }
        .selected-count{
            color: #999;
            margin-left: 400px;
        }
        .total{
            color: #999;
            margin-left: 65px;
        }
        button{
            
                width: 100px;
                background-color: #fdd000;
        }
    }
    .settlement{
        font-size: 18px;
        color: #332200;
    }
    .check{
        padding-left: 10px;
        width: 100px;
    }
    .info{
        width: 305px;
    }
    .support{
        width: 305px;
    }
    .price{
        width: 100px;
    }
    .count{
        width: 100px;
        text-align: center;
    }
    .unit{
        width: 100px;
        text-align: center;
    }
    .amount{
        width: 100px;
    }
    .del{
        width: 50px;
    }
    .del-icon{
        width: 20px;
        height: 20px;
    }
    .partInfo{
        width: 380px;
        color: #333;
    }
    .brand{
        margin-left: 24px;
    }

    .inquiryItem{
        .header{
            height: 40px;
            line-height: 40px;
            padding-left: 10px;
            background-color: #fafafa;
            border: 1px solid #e0e0e0;
            border-bottom: none ;
            // border-bottom: solid 1px #e0e0e0;
        }
        .partItem:last-of-type{
            border-bottom: 1px solid #e0e0e0;
        }
           
    }
    .box{
        height: 20px;
        line-height: 20px;
        font-size: 12px;
        color: #999;
        padding: 2px 5px;
        border: 1px solid #e5e5e5;
        margin: 0 5px;

    }
    .partItem{
        padding: 10px;
        border: 1px solid #e0e0e0;
        border-bottom: none;
        // border-top: 1px solid #e0e0e0;
    }
    .partItem:hover+.partItem{
        border-top: none  ;
    }
    
}