.inside-header{
  width: 100%;
  background-color: #fdd000;
}
.inside-header .sign-out{
  display:block;
  height:30px;
  font-size: 14px;
  color: #666;
  line-height: 30px;
  background-color: #fff;
}
.sign-out .sign-out-btn{
  margin-left: 26px;
  padding:0 6px;
  cursor:pointer;
}
.inside-header .header-banner{
  display:block;
}
.header-banner .banner-left{
  font-size: 0;
  float: left;
  padding:17px 0 15px;
}
.header-banner .banner-left .logo{
  display: inline-block;
  width:96px;
  height:28px;
  margin-right: 16px;
  vertical-align: bottom;
  background:url('../../static/image/logo.png') no-repeat;
  background-size:96px 28px;
}
.header-banner .banner-left .title{
  display: inline-block;
  height:28px;
  font-size: 16px;
  color: #332200;
  line-height: 28px;
}
.header-banner .banner-right{
  float: right;
  display: flex;
  height:60px;
  line-height: 60px;
}
.header-banner .banner-right .service-item{
  font-size: 14px;
  position: relative;
}
.banner-right .service-item a{
    display: inline-block;
    height: 100%;
  color: #664400;
  padding:0 23px 0;
}

.banner-icon{
  position: absolute;
  bottom:0;
  left:50%;
  width: 20px;
  height: 20px;
  margin-left:-10px;
  overflow: hidden;
}
.banner-icon .banner_hover{
  position: absolute;
  border:10px solid #fdd000;
  // border-bottom-color: #fff;
}
.header-banner .banner-right .selected .banner_hover{
  border-bottom-color: #fff;
}
.header-banner .banner-right .service-item:hover{
  cursor: pointer;
  background-color: #2b2300;
}
.header-banner .banner-right .service-item:hover a{
  color: #fdd000;
}
.header-banner .banner-right .service-item:hover .banner_hover{
  border-color: #2b2300;
} 