

// 车辆信息
.car-wrap .car-info{
  display:flex;
  align-items: center;
  padding:20px 0px 20px 27px;
}

.car-wrap .car-info .car-logo{
  width: 64px;
  height:64px;
  margin-right: 20px;
}
.carinfo-wrap{
  flex: 1;
}
.car-wrap .car-info .car-desc{
  font-size: 16px;
  line-height: 16px;
  color:#333;
  margin-bottom:10px;
}
.car-info .info-wrap{
  display: flex;
  flex-wrap: wrap;
}
.car-info .info-item{
  font-size: 0px;
  margin-top:11px;
  margin-right: 25px;
}
.car-info .info-item:nth-child(6){
  margin-right: 0;
}
.car-info .info-item .item-name{
  display: inline-block;
  font-size: 14px;
  height: 34px;
  line-height: 12px;
  padding: 11px 17px;
  border: 1px solid #ccc;
  border-right: none;
  background-color: #fafafa;
  color:#333;
}
.car-info .info-item .item-value{
  display: inline-block;
  font-size: 14px;
  height: 34px;
  line-height: 12px;
  padding: 11px 17px;
  border: 1px solid #ccc;
  vertical-align: top;
}