.parts-quality{
  display: flex;
  align-items: center;
  height: 54px;
  padding: 0 20px;
  border-bottom: 10px solid #fafafa;
}
.quality-check{
  margin-right: 22px;
  color: #333;
}
.invoice{
  color: #333;
  margin-left: 50px;
  margin-right: 12px;
}
.retract{
  height: 44px;
  margin-left: 100px;
  line-height: 44px;
  cursor: pointer;
}
.retract:hover{
  color: #fdd000;
}
.retract-up{
  transform: rotate(-180deg);
  transition: All 0.4s ease-in-out;
}
.retract-down{
  transition: All 0.4s ease-in-out;
}
// 配件列表
.parts-wrap {
  padding: 10px 20px;
}
.parts-wrap .parts-single{
  font-size: 14px;
  color: #999;
}
.parts-single:last-child{
  border-bottom: 1px solid #dbdbdb;
}
.single-info{
  height: 48px;
  line-height: 48px;
  font-size: 16px;
  color: #333;
  background-color: #fafafa;
  border-left:1px solid #dbdbdb;
  border-right:1px solid #dbdbdb;
  border-top:1px solid #dbdbdb;
}
.single-info .single-order{
  float: left;
  width: 50px;
  margin-right: 10px;
  font-size: 18px;
  text-align: center;
  color: #333;
  background-color: #fdd000;
}
.single-info .number{
  margin-right: 26px;
}
.single-info .anchor{
  opacity: 0;
  transition: opacity .3s ease;
}
.origin-parts{
  box-sizing: border-box;
  height: 40px;
  line-height: 38px;
  padding: 0 15px;
  border:1px  solid #dbdbdb;
  border-bottom: none;
}
.origin-parts .origin-number{
  margin-right: 48px;
}
.origin-parts .drow-up{
  float: right;
  cursor: pointer;
}
.conmmen-name .parts-item{
  display: flex;
  align-items: center;
  min-height: 40px;
  padding: 0 15px;
  border:1px  solid #dbdbdb;
  border-top-width: 0;
  color: #999;
}
.conmmen-name .parts-item:last-child{
  border-bottom-width: 0;
}
.parts-item .parts-price{
  width: 96px;
  padding-left: 20px;
  color: #ff3333;
}
.parts-item .brand{
  width: 100px;
  color: #666666;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.parts-item .store-info{
  width: 100px;
}
.parts-item .address{
  width: 226px;
  color: #666;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.parts-item .parts-tip{
  min-width: 296px;
  margin-right: 16px;
  font-size: 0;
}
.parts-item .parts-tip span{
  display: inline-block;
  font-size: 12px;
  padding: 4px 4px;
  border:1px solid #e5e5e5; 
  margin-right: 4px;
  margin-bottom: 4px;
  border-radius: 4px;
}
.parts-item .parts-tip span:last-child{
  margin-right: 0;
}
.parts-item .msg{
  font-size: 24px;
  padding: 6px;
  cursor: pointer;
}
.parts-item .msg:hover{
  color: #ff762b;
}
.parts-item .phone{
  font-size: 24px;
  padding: 6px;
  cursor: pointer;
}
.parts-item .phone:hover{
  color: #ff762b;
}

.oe-none{
  display: none;
}
.none-price{
  height: 42px;
  line-height: 42px;
  text-align: center;
  border-left: 1px solid #dbdbdb;
  border-right: 1px solid #ddbbdd;
}