.madel-wrap .ant-modal-content .ant-modal-body{
  padding: 20px 0 20px 20px ;
}
.ant-tag{
  height: 34px;
  line-height: 34px;
}
.madel-wrap{
  height: 260px;
  overflow: auto;
}
// 字母选择
.select-brand{
  font-size: 14px;
  line-height: 14px;
  color: #666;
} 
.select-brand .letter{
  margin-bottom: 10px;
  color: #333;
}
.letter-wrap{
  font-size: 0;
  margin-bottom: 20px;
}
.letter-wrap .letter-item{
  display: inline-block;
  font-size: 14px;
  line-height: 30px;
  padding: 0 8px;
  margin-right: 3px;
  cursor: pointer;
}
.letter-wrap .letter-item:hover{
  background-color: #fdd000;
}
.letter-wrap .letter-active{
  background-color: #fdd000;
  color: #333;
}
// 图标选择
.img-wrap{
  display: flex;
  flex-wrap: wrap;
}
.img-wrap .cars-img{
  box-sizing: border-box;
  width: 143px;
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 14px;
  border: 1px solid #ddd;
  padding-left: 6px;
  margin: 0 10px 10px 0;
  cursor: pointer;
}
.img-wrap .cars-img:nth-child(5n){
  margin-right: 0;
}
.img-wrap .cars-img:hover{
  border-color: #fdd000;
}
.img-wrap .cars-img img{
  width: 32px;
  height: 32px;
  margin-right: 6px;
}

// 已选
.selected-wrap{
  display: flex;
  height: 34px;
  line-height: 34px;
  margin-bottom: 10px;
}
.selected-wrap .nav-car-btn{
  box-sizing: border-box;
  display: flex;
  height: 34px;
  border: 1px solid #dbdbdb;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  margin-right: 10px;
}
.nav-car-btn .nav-close{
  font-size: 18px;
  margin-left: 10px;
  cursor: pointer;
}
.made-in{
  margin-top:10px;
}
.made-name{
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #1a1a1a;
}
.made-name i{
  width: 8px;
  height: 8px;
  border-radius: 4px;
  margin-right: 6px;
	background-color: #000000;
}
.made-in .made-car{
  display: inline-block;
  box-sizing: border-box;
  height: 40px;
  line-height: 40px;
  text-indent: 10px;
  min-width: 140px;
  margin-top: 10px;
  margin-right: 10px;
  padding-right: 10px;
  border: 1px solid #f5f5f5;
}
.made-in .made-car:hover{
  border-color: #fdd000;
  cursor: pointer;
}
.style-wrap{
  display: flex;
  flex-wrap: wrap;
}
.car-style{
  box-sizing: border-box;
  min-width: 240px;
  height: 40px;
  line-height: 40px;
  border: 1px solid #f5f5f5;
  margin-right: 8px;
  margin-top: 10px;
  padding-right: 10px;
  text-indent: 10px;
}
.car-style:hover{
  border-color: #fdd000;
  cursor: pointer;
}
.style-wrap .car-style:nth-child(3n){
  margin-right: 0;
}