.order.container {
    padding: 20px 20px 40px;
    margin: 20px auto;
    font-size: 14px;
    color: #666;
    background-color: #fff;
    h3 {
        font-size: 16px;
        line-height: 16px;
        color: #1a1a1a;
        margin-bottom: 10px;
        .addAddress {
            font-size: 1px;
            color: #d47e4f;
            cursor: pointer;
        }
    }
    .adress {
        margin-bottom: 30px;
        .address-content {
            padding: 15px 10px;
            // height: 80px;
            border: solid 1px #e0e0e0;
            .ant-radio-group{
                width: 100%;
            }
            .address-items{
                height: 25px;
                overflow: hidden;
                .address-item {
                    margin-bottom: 8px;
                    .address-operation {
                        margin-right: 14px;
                        .del {
                            margin-left: 30px;
                        }
                    }
                }
            }
            .address-items.more{
                height: auto;
                overflow:auto;
            }
            
            .arrow-bottom {
                display: inline-block;
                transform: rotate(90deg)
            }
            .showLess{
                position: relative;
                right: 15px;
            }
            .icon-xiala{
                font-size: 10px;
                margin-left: 8px;
            }
        }
    }
    .partsInfo {
        .partsInfo-content {
            // border-bottom: none;
            .partsInfo-con-header {
                border: 1px solid #e0e0e0;
                border-bottom: none ;
                height: 34px;
                line-height: 34px; // border-bottom: solid 1px #e0e0e0;
                display: flex;
            }
        }
    }
    .order-footer {
        display: flex;
        justify-content: space-between;
        .first {
            color: #666;
        }
        .second {
            font-size: 16px;
        }
        .totalMount {
            margin-right: 20px;
            font-size: 16px;
        }
        button {
            width: 136px;
            height: 44px;
            background-color: #fdd000;
            border-radius: 4px;
            font-size: 18px;
            color: #332200;
        }
    }
    .part,
    .partInfo {
        width: 500px;
        padding-left: 12px;
    }
    .price,
    .count,
    .unit,
    .amount {
        width: 165px;
        text-align: center;
    }
    .amount {
        width: 160px;
    } //公司
    .companyItem {
        border: 1px solid #e0e0e0;
        margin-bottom: 20px;
        .header {
            padding-left: 12px;
            height: 34px;
            line-height: 34px;
            background-color: #fafafa;
            border-bottom: solid 1px #e0e0e0;
        }
        .partItem {
            height: 84px;
            border-bottom: 1px solid #e0e0e0;
            display: flex;
            align-items: center;
            .partInfo {
                color: #666;
                &>div {
                    margin: 8px;
                }
            }
        }
        .footer {
            .item {
                padding-left: 12px;
                height: 34px;
                line-height: 34px; // border-bottom: 1px solid #e0e0e0;
                .subamount {
                    margin-right: 45px;
                }
                .subtotal {
                    margin-right: 20px;
                }
                input {
                    height: 32px;
                    width: 1080px;
                }
            }
        }
    }
    
}
.address-cascader{
   
    .ant-cascader-picker-label,.ant-cascader-picker-arrow,.ant-cascader-picker-clear{
        top: 18px;
    }
}
.ant-cascader-menu{
    min-width: 190px;
}

.dialog {
    .label {
        width: 80px;
    }
    .btn {
        .ant-btn {
            margin-left: 80px;
            width: 147px;
            height: 44px;
            background-color: #fdd000;
            border-radius: 4px;
            color: #332200;
        }
    }
    .ant-input {
        width: 300px;
        height: 34px;
        line-height: 34px;
        border: solid 1px #e0e0e0;
        margin-bottom: 12px;
    }
    .ant-modal-content {
        width: 500px;
    }
    .ant-modal-body {
        padding: 20px 75px 50px 40px;
    }
}