.slider {
    height: 46px;
    border-radius: 4px;
    display: inline-block;
    position: relative;
    .pop,
    .moving{
        position: absolute;
        height: 46px;
        line-height: 46px;
        text-align: center;
        left: 0;
        top: 0;
    }
    .pop {
        background-color: #dbdbdb;
        color: #ffffff;
        z-index: 1;
        border-radius: 0px 4px 4px 0px;
    }
    .moving {
        color: #fff;
        background-color: #40cd31;
        z-index: 2;
        border-radius: 4px 0px 0px 4px;
    }
    button {
        position: absolute;
        width: 44px;
        height: 46px;
        background-color: #ffffff;
        border-radius: 4px 0px 0px 4px;
        border: solid 1px #b2b2b2;
        z-index: 3;
        top: 0;
        left: 0;
        cursor: pointer;
    }
    button.completed{
        border-radius: 0px 4px 4px 0px;
    }

}