.container.inquery{
  margin-top: 20px;
}
.inquery-btn{
  text-align: center;
  padding-bottom: 46px;
}
.ant-btn{
  height: 34px;
}
.price-btn{
  margin-right: 40px;
}
.none-data{
  height: 100%;
  line-height: 100%;
  text-align: center;
  font-size: 36px;
}
.car-wrap{
  background-color: #fff;
  font-size: 16px;
  margin-bottom: 20px;
}
.car-wrap .module-info{
  display: flex;
  height: 48px;
  border-bottom: 1px solid #f2f2f2;
  justify-content: space-between;
  align-items: center;
  padding:0 22px 0 10px;
}
.car-wrap .module-info .module-name{
  height: 15px;
  line-height: 15px;
  border-left: 4px solid #fdd000;
  text-indent: 5px;
  color:#1a1a1a;
}

// 搜索车
.car-wrap .search-car{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height:83px;
  padding:19px 21px 24px 28px;
  border-bottom: 4px solid #fafafa;
}
// .search-car .serach-wrap{
//   padding:19px 21px 24px 28px;
// }
.search-car .ant-input{
  box-sizing: border-box;
  height: 35px;
  width:249px;
  border-radius: 0; 
  margin-left:10px;
}
.search-car .serach-wrap .search-btn{
  width: 65px;
  height: 36px;
  border-radius: 0; 
  margin-right:27px;
  background-color: #fdd000;
  border-color: #fdd000;
  color:#332200;
}
.search-car .serach-wrap .perhaps{
  color: #332200;
  margin-right: 27px;
}
.search-car .serach-wrap .select-car-btn{
  width: 120px;
  height:36px;
  line-height: 34px;
  background-color: #fff;
  color:#333;
  border: 1px solid #dbdbdb;
  border-radius: 4px;
  cursor: pointer;
}
.search-car .serach-wrap .icon-car{
  font-size: 20px;
  margin-right: 8px;
}
.search-car .car-number{
  color:#1a1a1a;
  margin-right:6px;
}
//要求
.requirement{
  background-color: #fff;
  font-size: 16px;
  margin-bottom: 20px;
}
.requirement .module-info{
  display: flex;
  height: 48px;
  border-bottom: 1px solid #f2f2f2;
  justify-content: space-between;
  align-items: center;
  padding:0 22px 0 10px;
}
.requirement .module-info .module-name{
  height: 15px;
  line-height: 15px;
  border-left: 4px solid #fdd000;
  text-indent: 5px;
  color:#1a1a1a;
}
.option-wrap{
  padding: 20px;
}
.option-name{
  float: left;
  width:86px;
  color:#333;
  height: 34px;
  line-height: 34px;
}
.opstions{
  display: flex;
  align-items: center;
  height: 34px;
}
.option-wrap .opstions:last-child{
  margin-bottom:0;
}
.opstions .shi-jian{
  font-size: 24px;
  margin-left: 6px;
}
// 联系
.contact{
  background-color: #fff;
  font-size: 16px;
  margin-bottom: 40px;
}
.contact .module-info{
  display: flex;
  height: 48px;
  border-bottom: 1px solid #f2f2f2;
  justify-content: space-between;
  align-items: center;
  padding:0 22px 0 10px;
}
.contact .module-info .module-name{
  height: 15px;
  line-height: 15px;
  border-left: 4px solid #fdd000;
  text-indent: 5px;
  color:#1a1a1a;
}
.contact-info{
  display:flex;
  align-items: center;
  height:76px;
  padding:20px;
}
.contact-info .ant-input{
  height:36px;
  width:295px;
}
.contact-info .contact-desc{
  color: #1a1a1a;
}
.contact-info .contact-space{
  margin-left: 40px;
}
//vin码
.vin-wrap{
  display: flex;
  width: 400px;
  height: 53px;
  align-items: center;
  border: 1px solid #dedede;
  padding-left: 20px;
  margin-bottom: 10px;
}
.vin-wrap img{
  width: 36px;
  height: 36px;
  margin-right: 10px;
}
.vin-wrap:hover{
  border-color: #fdd000;
  cursor: pointer;
}
.vin-wrap.vin-select{
  border-color: #fdd000;
}
.inquery .loading{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0,0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}