.icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
html,body,#app{margin: 0;font-size:12px;width: 100%;min-width:1200px;height: 100%;}
html{
  overflow-y: auto;
}
h3,h4,h5,h6{margin:0;}
p{
  margin:0;
}
a{text-decoration: none;color:#666666;}
a:link{text-decoration:none;}
ul,ol{margin:0;padding:0;}
li,ol{list-style: none;}
i{font-style: normal;}
dl,dd{
  margin-bottom: 0;
}
button{
  border-width:0;
  border-style:none;
  margin: 0;
  padding: 0; 
  outline: none;
  background-color: #fff;
}

input{
  text-rendering: auto;
  color: initial;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  border: none;
  outline: none;
  padding: 0;
}
input:focus{
    outline: none;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
        -webkit-appearance: none;
        appearance: none;
        margin: 0;
}

/* 全局容器大小 */
#root{
  background-color: #fafafa;
}
.container{
  width: 1200px;
  margin:0px auto;
}
/* 清除浮动 */
.clearfix:after{display:block;clear:both;content:"";visibility:hidden;height:0}

/* 覆盖ant组件样式 */
.ant-table-tbody > tr > td{
  padding: 10px
}
.ant-input:hover {
  border-color: #fdd000;
  border-right-width: 1px !important;
}
.ant-input:focus {
  border-color: #fdd000;
  outline: 0;
  -webkit-box-shadow: 0 0 0 2px rgba(253,208,0, 0.2);
          box-shadow: 0 0 0 2px rgba(253,208,0, 0.2);
  border-right-width: 1px !important;
}
.ant-input-affix-wrapper:hover .ant-input:not(.ant-input-disabled){
  border-color: #fdd000;
}

.ant-btn{
  color:#1a1a1a;
}
.ant-btn:hover,
.ant-btn:focus {
  color: #333;
  border-color: #fdd000;
}
.ant-btn:active,
.ant-btn.active {
  color: #333;
  border-color: #fdd000;
}
.ant-btn-primary {
  background-color: #fdd000;
  border-color: #fdd000;
  -webkit-box-shadow: 0 2px 0 rgba(253,208,0, 0.045);
          box-shadow: 0 2px 0 rgba(253,208,0, 0.045);
}
.ant-btn-primary:hover,
.ant-btn-primary:focus {
  background-color: #fdd000;
  border-color: #fdd000;
}
.ant-btn-primary:active,
.ant-btn-primary.active {
  background-color: #fdd000;
  border-color: #fdd000;
}
.ant-checkbox-wrapper:hover .ant-checkbox-inner,
.ant-checkbox:hover .ant-checkbox-inner,
.ant-checkbox-input:focus + .ant-checkbox-inner {
  border-color: #fdd000;
}
.ant-checkbox-checked .ant-checkbox-inner {
  background-color: #fdd000;
  border-color: #fdd000;
}
.ant-checkbox-disabled.ant-checkbox-checked .ant-checkbox-inner:after {
  -webkit-animation-name: none;
          animation-name: none;
  border-color: rgba(253,208,0, 0.25);
}
.ant-checkbox-indeterminate .ant-checkbox-inner:after {
  background-color: #fdd000;
}

.ant-radio-wrapper:hover .ant-radio .ant-radio-inner,
.ant-radio:hover .ant-radio-inner,
.ant-radio-focused .ant-radio-inner {
  border-color: #fdd000;
}
.ant-radio-checked:after {
  border: 1px solid #fdd000;
}
.ant-radio-inner:after {
  background-color: #fdd000;
}
.ant-radio-wrapper:hover .ant-radio, .ant-radio:hover .ant-radio-inner, .ant-radio-input:focus + .ant-radio-inner,
.ant-radio-checked .ant-radio-inner {
  border-color: #fdd000;
}
.ant-calendar-picker:hover .ant-calendar-picker-input:not(.ant-input-disabled),
.ant-select-auto-complete.ant-select .ant-input:focus,
.ant-select-auto-complete.ant-select .ant-input:hover {
  border-color: #fdd000;
}
.ant-select-selection:hover {
  border-color: #fdd000;
}

.ant-select-focused .ant-select-selection,
.ant-select-selection:focus,
.ant-select-selection:active {
  border-color: #fdd000;
  box-shadow: 0 0 0 2px rgba(253,208,0, 0.25);
}

.ant-btn-primary:hover,.ant-btn:hover{
  background-color: #ffd928!important;
  color: #332200!important;
}
.ant-input-number:hover {
  border-color: #ffd928;
}
.ant-input-number:focus {
  border-color: #ffd928!important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 2px rgba(253,208,0, 0.045);
          box-shadow: 0 0 0 2px rgba(253,208,0, 0.045);
  border-right-width: 1px !important;
}
.ant-input-number-handler:hover .ant-input-number-handler-down-inner {
  color: #ffd928;
}
.mr8 {
    margin-right: 8px;
}

.mr5 {
    margin-right: 5px;
}

.mr15 {
    margin-right: 15px;
}
.mr20{
    margin-right: 20px;
}
.warn {
    color: #f33
}

.link {
    color: #3377cc;
    cursor: pointer;
}
.dib{
    display: inline-block;
}
.fl{
    float: left;
}
.fr{
    float: right;
}
.box{
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    color: #999;
    padding: 2px 5px;
    border: 1px solid #e5e5e5;
    margin: 0 5px;
}
/* 垂直居中 */
.vAlignMiddle{
    display: flex;
    align-items: center
}
.cursor{
    cursor: pointer;
}
/* div hover样式 */
#root .box-hover:hover{
    border: 1px solid #fdd000;
}