.timeLine {
    position: relative;
    font-size: 12px;
    .timeline-item-tail {
        border-radius: 50%;
        position: absolute;
    }
    .timeline-item-tail.main {
        width: 11px;
        height: 11px;
        position: relative;
        border: 2px solid #ffeea2;
        // ffeea2
        left: -2px;
        border-radius: 50%;
        .timeline-item-tail-child {
            display: inline-block;
            width: 7px;
            height: 7px;
            border-radius: 50%;
            background-color: #fdd000;
            position: relative;
            top: -7px;
            // fdd000
        }
    }
    .timeline-item-tail.sub {
        width: 6px;
        height: 6px;
        background-color: #e0e0e0;
    }
    .timeline-item-content {
        position: relative;
        top: -2px;
    }
    .timeline-item-content.main {
        color: #333;
        margin-left: 20px;
        position: relative;
        top: -10px;
    }
    .timeline-item-content.sub {
        color: #999;
        margin-left: 113px;
    }
    .timeline-item-head {
        height: 100%;
        position: absolute;
        left: 2px;
        top: .75em;
        height: 100%;
        border-left: 2px solid #e8e8e8;
    }
}