.cashier{
    font-size: 14px;
    color: #4d4d4d;

    .cashier-header{
        font-size: 24px;
        height: 22px;
        line-height: 31px;
        margin: 20px 0 10px;
        i{
            margin-right: 5px;
            display: inline-block;
                width: 4px;
                height: 20px;
                background-color: #fdd000;
        }
    }

    .cashier-con{
        background-color: #fff;
        .con-header{
            padding:0  20px 0 15px;
            height: 64px;
            line-height: 64px;
            border-bottom: 1px solid #e0e0e0;
            .fr{
                display: flex;
                align-items: center;
                strong{
                    font-size: 24px;
                    margin-left: 16px;
                }
            }
            
        }
        .pay-item{
            
            height: 50px;
            line-height: 50px;
            padding:0  20px 0 15px;
            border-bottom: 1px solid #e6e6e6;
            img{
                margin: 0 10px 0 15px;
                display: inline-block;
                width: 20px;
                height: 20px;
                background-color: #333;
            }
            .credit{
                color: #999;
            }
        }
        .pay{
            padding:20px  20px 140px 15px;
            text-align: right;
            button{
                    font-size: 18px;
                    color: #332200;
                    width: 136px;
                    height: 44px;
                    background-color: #fdd000;
                    border-radius: 4px;
            }
        }
        .ant-radio-group{
            display: block;
        }
    }
    .ml20{
        margin-left: 20px;
    }
}